<template>
  <div id="app">
	<no-page v-show="$root.bLoading"></no-page>
    <app-header v-if="$root.bHead"></app-header>
	<keep-alive :include="['home','serch','goods']">
		<router-view></router-view>
	</keep-alive>
	<app-footer v-if="$root.bFoot"></app-footer>
  </div>
</template>

<script>
import NoPage from '../pages/no-page.vue'
import AppHeader from './header.vue'
import AppFooter from './footer.vue'
export default {
  name: 'app',
  data(){
	  return {
	  }
  },
  components: {
    AppHeader,AppFooter,NoPage
  },
  methods:{
  },
  watch:{
	  $route:{
		  handler(nextValue){		  
			  let path = nextValue.path;
			  if(/home/.test(path)){				
			    this.$root.bHead=this.$root.bFoot=true;
			  }
			  if(/user|serch|goods|no-page/.test(path)){
				this.$root.bHead=false;
			    this.$root.bFoot=true;
			  }
			  if(/login|reg/.test(path)){
				this.$root.bHead=this.$root.bFoot=false;
			  }
		  },
		  immediate: true 
	  }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.ycol{
	color: pink;
}
</style>
