<template>
	<div class="reg">
		<div>
			<van-nav-bar
			  left-text="返回"
			  left-arrow
			  @click-left="onClickLeft"
			/>
		</div>
		<div>
			<van-tabs v-model="active">
			  <van-tab title="登录" to="/login" name="login"></van-tab>
			  <van-tab title="注册" to="/reg" name="reg"></van-tab>
			</van-tabs>
		</div>
		<div>
			<van-field v-model="username" label="账号" placeholder="请输入用户名" required/>
			<van-field v-model="tel" type="tel" label="手机号" placeholder="请输入手机号" required/>
			<van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
			  <template #button>
			    <van-button size="small" type="primary" color="pink">发送验证码</van-button>
			  </template>
			</van-field>
			<van-field v-model="password" type="password" label="密码" placeholder="请输入密码" required/>
		</div>
		<div class="butn">
			<van-button type="primary" to="" block round color="paleturquoise" @click="reg">注 册</van-button>
		</div>
		
	</div>
</template>
<script>
	
	export default{
		name:'reg',
		data(){
			return {
				tel: '',
				username: '',
				password: '',
				sms:'',
				active: 'reg',
			}
		},
		components:{},
		methods:{
			onClickLeft() {
			      this.$router.go(-1);
			    },
				reg(){
					let formData = new FormData();
					formData.append('username',this.username);
					formData.append('password',this.password);
				this.$axios({
				  url:'reg',
				  method:'post',
				  data:formData
				}).then(
				  res=>{
				    // console.log(0.5,res)
				    if(res.data.err===0){
				      this.$router.push('/login')
				    }else{
				      this.mess = res.data.msg
				    }
				  }
				)
				}
		},
		computed:{},
		mounted(){},
		updated(){},
		
	}
</script>
