<template>
	<div class="login">
		<div>
			<van-nav-bar
			  left-text="返回"
			  left-arrow
			  @click-left="onClickLeft"
			/>
		</div>
		<div>
			<van-tabs v-model="active">
			  <van-tab title="登录" to="/login" name="login"></van-tab>
			  <van-tab title="注册" to="/reg" name="reg"></van-tab>
			</van-tabs>
		</div>
		<div>
			<van-field v-model="username" label="账号" placeholder="请输入用户名" required/>
			<van-field v-model="tel" type="tel" label="手机号" placeholder="请输入手机号" required/>
			<van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
			  <template #button>
			    <van-button size="small" type="primary" color="pink">发送验证码</van-button>
			  </template>
			</van-field>
			<van-field v-model="password" type="password" label="密码" placeholder="请输入密码" required/>
		</div>
		<div class="butn">
			<van-button type="primary" to="" block round color="paleturquoise" @click="onlogin">登 录</van-button>
		</div>
		
	</div>
</template>
<script>
	
	export default{
		name:'login',
		data(){
			return {
				tel: '',
				username: '',
				password: '',
				sms:'',
				active: 'login',
			}
		},
		components:{},
		methods:{
			onClickLeft() {
			      this.$router.go(-1)
			    },
			onlogin(){
				this.$axios({
				  url:'login',
				  method:'post',
				  data:{
				    username: this.username,
				    password: this.password,
				  }
				}).then(
				  res=>{
				    if(res.data.err===0){
				      //种token
				      window.localStorage.setItem('user',JSON.stringify(res.data))
				      if(this.$route.query.p){
				        this.$router.push(this.$route.query.p)
				      }else{
				        this.$router.push('/user')
				      }
				    }else{
				      console.log(2);
				      this.mess = res.data.msg
				    }
				  }
				)
			}
		},
		computed:{},
		mounted(){},
		updated(){},
		
	}
</script>
<style>
	.van-nav-bar__text{font-size: 0.3rem;}
	.van-nav-bar__content .van-nav-bar__title{font-size: 0.3rem;color:blue;}
	.butn{display: flex;justify-content:center;padding: 0 1rem ;margin-top: 1rem;}
	.butn .van-button--normal{font-size: 18px;}
	.van-nav-bar__content{background-color:plum; color: white;}
	.van-nav-bar__left .van-nav-bar__text{color:white}
	.van-nav-bar .van-nav-bar__content .van-icon{color:white;}
</style>
