<template>
	<div class="home paddingbottom" v-title :data-title="$route.meta" >
		<div>
			<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
			<van-swipe-item v-for="n of banner" :key="n.id"><img :src="n.banner"></van-swipe-item>
			</van-swipe>
		</div>
		<div>
			<van-card

			  :price="n.price"
			  :desc="n.desc"
			  :title="n.title"
			  :thumb="n.thumb"
			  v-for="n of content"
			  @click="$router.push(`/goods/${n._id}?collectionName=foods`)"
			>
			  <template #tags>
			    <van-tag plain type="danger">{{n.tag}}</van-tag>
			    <van-tag plain type="danger">{{n.teg}}</van-tag>
			  </template>
			</van-card>
		</div>
	</div>
</template>
<script>
	export default{
		name:'home',
		data(){
			return {
				banner:'',
				content:'',
			}
		},
		components:{},
		methods:{},
		computed:{},
		deactivated(){},
		activated(){
			axios.all([
			  axios({url:'/goods/banner',params:{_limit:4,_sort:1}}), 
			  axios({url:'/goods/collow',params:{_limit:4,_sort:1}})
			]).then(axios.spread((banner, collow)=>{
				banner.data.data.map((item,index)=>{if(!item.banner.includes('http')){item.banner = this.$baseUrl + item.banner;}});
				collow.data.data.map((item,index)=>{if(!item.thumb.includes('http')){item.thumb = this.$baseUrl + item.thumb;}});
				this.banner=banner.data.data;
				this.content=collow.data.data;
			}))
		},
		mounted(){
		},
		updated(){},
	}
</script>
<style scoped>
	.my-swipe .van-swipe-item {
	    color: #fff;
	    text-align: center;
	    background-color: #39a9ed;
	  }
	.van-swipe-item>img{
		width: 100%;
		height: 3rem;
		display: block;
	}
</style>
