<template>
	<div class="serch">
		<div>
			<van-search
			  background="mediumaquamarine"
			  v-model="value"
			  show-action
			  placeholder="请输入商家名称"
			  @search="onSearch"
			>
			  <template #action>
			    <div @click="onSearch">搜索</div>
			  </template>
			</van-search>
		</div>
		<div>
			<van-card
			  :price="n.price"
			  :desc="n.desc"
			  :title="n.title"
			  :thumb="n.thumb"
			  v-for="n of content"
			  @click="$router.push(`/goods/${n._id}?collectionName=foods`)"
			>
			  <template #tags>
			    <van-tag plain type="danger">{{n.tag}}</van-tag>
			    <van-tag plain type="danger">{{n.teg}}</van-tag>
			  </template>
			</van-card>
		</div>
	</div>
</template>

<script>
	export default{
		name:'serch',
		data() {
		    return {
		      value: '',
			  content:'',
		    };
		  },
		  methods: {
		    onSearch() {
		      console.log(this.value)
		    },
		  },
		  deactivated(){},
		  activated(){
			  this.$axios({
			  // axios({
			    url:'/goods/collow',
			    params:{
			      _limit: 4,
			  	_sort:1,
			    },
			  }).then(
			    res=>{
			  res.data.data.map((item,index)=>{
			    if(!item.thumb.includes('http')){
			      item.thumb = this.$baseUrl + item.thumb;
			    }
			  })
			  this.content=res.data.data
			  		}
			  )
		  },
		  mounted(){
		  }
	}
</script>

