import Vue from 'vue'
//1. 引入路由包
import VueRouter from 'vue-router'
//2. 安装插件包到Vue上, 
Vue.use(VueRouter);

import home from '../pages/home.vue'
import detail from '../pages/detail.vue'
import goods from '../pages/goods.vue'
import login from '../pages/login.vue'
import reg from '../pages/reg.vue'
import NoPage from '../pages/no-page.vue'
import user from '../pages/user.vue'
import serch from '../pages/serch.vue'
//路由配置
let routes=[
	{name:'home',path:'/home',component:home,meta:'首页'},
	{path:'/user',component:user,meta:'用户'},
	{path:'/goods',component:NoPage,meta:'商品'},
	{path:'/serch',component:serch,meta:'搜索'},
	// {path:'/goods/:_id',component:detail},
	{
		path:'/goods',
		component:goods,
		children:[
			{name:'goods',path:':_id',component:detail,meta:'详情'}
		]
	},
	{path:'/reg',component:reg,meat:'注册'},
	{path:'/login',component:login,meta:'登录'},
	// {path:'/',components:{
	// 	default:home,
	// 	a:goods,
	// 	b:user
	// }},
	{path:'/',redirect:'/home'},
	{path:'*',component:NoPage,meta:'公共'},
]


let router = new VueRouter({
	mode:'history',
	scrollBehavior (to, from, savedPosition) {
	    return { x: 0, y: 0 }
	  },
  routes  
});
router.beforeEach((to, from, next) => {
  // 守卫业务
  let token = window.localStorage.getItem('user')?true:false;
  if(to.path=='/login' || to.path=='/reg'){
    //判断是不是登录了
    //axios请求 携带 token 
    next()
  }else if(token){
	next();
  }else{
	  next('/login')
  }
})

export default router
