<template>
	<div class="goods" v-title :data-title="$route.meta">
		<div>
			<van-nav-bar
			  left-text="返回"
			  left-arrow
			  @click-left="onClickLeft"
			/>
		</div>
		<div style="position: absolute;left:0;right:0;margin:-30px auto;width: 74px;background-color: white;z-index: 999;border: 1px solid #CCCCCC;">
			<van-image
			  width="74"
			  height="74"
			  :src="picture"
			/>
		</div>
		<div style="padding-top: 50px;background: white;">
			<van-cell is-link @click="showPopup" class="vancell">{{title}}</van-cell>
			<van-popup v-model="show" round closeable close-icon-position="bottom-center">
				<template>
					<div>
						<p style="text-align: center;font-size: 20px;font-weight: bold;padding: 24px;">{{title}}</p>
						<div style="display: flex;justify-content: space-around;" class="mouder">
							<div><p>3.5</p><i>评分</i></div>
							<div><p>90单</p><i>月售</i></div>
							<div><p>好吃转送</p><i>约28分钟</i></div>
							<div><p>4元</p><i>配送费用</i></div>
							<div><p>1公里</p><i>距离</i></div>
						</div>
						<h2 style="padding: 10px;margin-top: 20px;">公告</h2>
						<p style="padding: 10px;">是以粥为特色的中式营养快餐，自2004年10月18日创立“嘉和一品”品牌至今，不断优化管理，积极创新，立足于“贴近百姓生活，服务千家万户”</p>
					</div>
				</template>
			</van-popup>
			<div style="text-align: center;">3.5分 月售90单 好吃转送 约28分钟 距离 1公里</div>
			<van-tabs v-model="active">
			  <van-tab title="点餐">
				<van-tree-select height="112vw" :items="items" :main-active-index.sync="aitem">
				  <template #content>
					<div>
					<p style="border-left: 1px solid #CACACA;font-size: 14px;background: #CCCCCC;height: 24px;line-height: 24px;padding-left: 16px;">优惠</p>
				    <van-card
				      @click="$router.push('/goods')"
				      :price="n.price"
				      :desc="n.desc"
				      :title="n.title"
				      :thumb="n.thumb"
				      v-for="n of content"
				    >
				      <template #tags>
				        <van-tag plain type="danger">{{n.tag}}</van-tag>
				        <van-tag plain type="danger">{{n.teg}}</van-tag>
				      </template>
				    </van-card>
					</div>
				  </template>
				</van-tree-select>
			  </van-tab>
			  <van-tab title="评价">内容 2</van-tab>
			  <van-tab title="商家">内容 3</van-tab>
			</van-tabs>
		</div>
	</div>
</template>
<script>
	export default{
		name:'goods',
		data(){
			return {
				picture:'',
				show: false,
				active:0,
				aitem:0,
				title:'',
				items: [],
				content:[{id:1,price:'20.00',desc:'正宗牛肉汤不添加色素',title:'湖南牛肉汤',thumb:'/images/food1.jpg',tag:'名店',teg:'火爆'},{id:2,price:'35.00',desc:'牛肉拉面真不错',title:'兰州拉面',thumb:'/images/food2.jpg',tag:'小店',teg:'人多'},{id:3,price:'15.00',desc:'地三鲜里都有啥',title:'江西家常菜',thumb:'/images/food3.jpg',tag:'人少',teg:'环境好'},{id:4,price:'20.00',desc:'新疆大白菜吃了还要吃',title:'新疆手撕羊肉馆',thumb:'/images/food4.jpg',tag:'名店',teg:'火爆'}],
			}
		},
		components:{},
		methods:{
			onClickLeft() {
			      this.$router.go(-1)
			    },
			showPopup() {
				      this.show = true;
				    },
		},
		computed:{},
		deactivated(){},
		activated(){
			let _id = this.$route.params._id;
				let collectionName = this.$route.query.collectionName;
				this.$axios.post(`/goods/${collectionName}/${_id}`).then(
				  res=>{
				    if(!res.data.data.thumb.includes('http')){
				      res.data.data.thumb = this.$baseUrl + res.data.data.thumb;
				    }
				    this.items=res.data.data.obj;this.title=res.data.data.title;this.picture=res.data.data.thumb;
			})
		},
		mounted(){
	}
		
	}
</script>
<style >
	.vancell>.van-cell__value--alone{font-size: 20px;font-weight: bold;padding-left: 56px;text-align: center;}
	.van-cell__right-icon{font-weight: bold !important;}
	.van-cell--clickable{padding-right: 54px !important;}
	.van-popup{width: 346px;height: 346px;}
	.mouder>div>p{text-align: center;font-weight: bold;font-size: 16px;}
	.mouder>div>i{text-align: center;font-size: 14px;color: #CCCCCC;display: block;margin-top: 10px;}
	.van-popup__close-icon{font-size: 36px !important;left:148px;bottom: 0px;}
</style>
